.event-card {

  &__photo {
    width: 100%;
    margin-bottom: 15px;

    transition: transform 300ms ease-out;

    &:hover {
      cursor: pointer;

      transform: scale(1.01);
    }
  }

  &__info {
    @include on-desktop {
      margin-top: 50px;
    }

    @include on-large-screen {
      margin-top: 150px;
    }
  }

  &__name {
    margin-bottom: 10px;

    font-weight: normal;
    font-size: 28px;
    line-height: 100%;
    color: $text-color;
    
    @include on-tablet {
      margin-bottom: 15px;
    }
  }
  
  &__name::after {
    display: inline-block;
    content: "";
    height: 10px;
    width: 10px;

    background-color: $accent-2;
    transition: border-radius 300ms linear;

    @include on-tablet {
      height: 15px;
      width: 15px;
    }
  }

  &__name:hover {
    &::after {
      border-radius: 50%;
    }
  }

  &__link {
    &:hover {
      color: $accent-color;
    }
  }

  &__text {
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    color: #4e4e4e;
  }
}
